.db-advanced-search {
  transition: max-height 1s ease-in;
  max-height: 1500px;
  overflow: hidden;
  position: absolute;
  top: 30px;
  z-index: 100;
  left: 50px;
  width: 500px;
  background-color: #1d1b23;
  color: grey;
  padding: 15px; }

.db-advanced-search--hide {
  max-height: 0;
  transition: max-height .5s ease-out;
  transition-delay: .1s;
  display: none; }

.db-advanced-search__form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between; }

.db-advanced-search__title {
  margin-bottom: 30px;
  font-size: 25px; }

.db-advanced-search__title:after {
  content: '';
  display: block;
  width: 300px;
  height: 2px;
  background-color: grey; }

.db-advanced-search__main-params {
  display: flex;
  flex-direction: column;
  color: #a7b6b7;
  margin-right: 40px;
  width: 50%; }

.db-advanced-search__second_params {
  display: flex;
  flex-direction: column;
  color: #a7b6b7;
  margin-right: 40px;
  width: 30%; }

.db-advanced-search__submit_params {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 30%; }

.db-advanced-search__error {
  color: red;
  font-weight: 700; }

.db-advanced-search__img-upload {
  margin-left: 100px; }

.db-add-item__name {
  border-radius: 20px; }

.slider--yellow .rangeslider-horizontal .rangeslider__fill {
  background-color: #939e19; }

.slider--green .rangeslider-horizontal .rangeslider__fill {
  background-color: #579e18; }

.slider--yellow .rangeslider-horizontal .rangeslider__handle {
  background-color: #939e19; }

.slider--green .rangeslider-horizontal .rangeslider__handle {
  background-color: #579e18; }

.slider--yellow .rangeslider-horizontal .rangeslider__handle:after {
  background-color: #939e19; }

.slider--green .rangeslider-horizontal .rangeslider__handle:after {
  background-color: #579e18; }

.db-advanced-search__text-area {
  height: 100px;
  border-radius: 15px;
  padding: 5px; }

.db-advanced-search__reset {
  font-size: 20px;
  color: red; }

.db-advanced-search__label {
  margin-left: 10px;
  font-size: 25px; }



