.db-search {
  display: flex; }

.db-search__input {
  background: transparent;
  padding: 10px 20px;
  color: #a7b6b7;
  position: relative; }

.db-search__box {
  width: 40px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100%;
  cursor: pointer;
  margin-right: 10px; }

.db-search__box--black-box {
  background: #1d1b23;
  border: 1px solid darkgreen; }

.db-search__icon {
  color: black;
  font-size: 25px; }

.db-search__icon--red-icon {
  color: red; }

.db-search__container {
  display: flex; }



