.db-film {
  height: 350px;
  width: 220px;
  margin: 10px;
  cursor: pointer;
  transition: all .3s;
  position: relative; }

.db-film__image {
  width: 100%;
  height: 100%; }

.db-film:hover > .db-film__desc {
  opacity: 1; }

.db-poster__icon {
  position: absolute;
  opacity: 1;
  top: 20px;
  right: 20px;
  font-size: 35px;
  padding: 15px; }

.db-poster__icon-info {
  position: absolute;
  opacity: 1;
  top: 20px;
  right: 60px;
  font-size: 35px;
  padding: 15px; }

.db-film__desc-text {
  position: absolute;
  opacity: 0;
  transition: opacity .3s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: auto;
  bottom: 0;
  color: white;
  background-color: rgba(193, 31, 31, 0.75);
  padding: 5px 0;
  font-size: 12px;
  text-align: center;
  font-family: sans-serif; }

.db-poster__icon-info:hover + .db-film__desc-text {
  opacity: 1; }

.db-poster--green-border {
  border: 2px solid green; }

.db-poster__icon--green {
  color: greenyellow; }

.db-film__desc {
  position: absolute;
  opacity: 0;
  transition: opacity .3s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  bottom: 0;
  color: white;
  background-color: rgba(39, 99, 178, 0.75);
  font-size: 35px;
  text-align: center;
  font-family: sans-serif; }

.custom-hint {
  padding: 10px;
  border: 1px solid red;
  height: 20px;
  width: 20px; }



