.db-movie {
  color: grey; }

.db-movie__container {
  width: 90%;
  margin-left: 5%; }

.db-movie__image {
  width: 200px;
  height: 300px;
  margin-right: 30px; }

.db-movie__image img {
  width: inherit;
  height: inherit; }

.db-movie__flex {
  display: flex;
  flex-wrap: wrap; }

.db-movie__flex--column {
  flex-direction: column;
  width: 300px;
  flex-wrap: wrap; }

.db-movie__desc {
  overflow: auto;
  height: 250px;
  width: 100%; }

.db-movie__name {
  margin: 40px 0;
  font-size: 25px; }

.db-movie__recommended {
  display: flex;
  width: 100%;
  overflow-x: scroll;
  height: 400px; }

.slider--yellow .rangeslider-horizontal .rangeslider__fill {
  background-color: #939e19; }

.slider--green .rangeslider-horizontal .rangeslider__fill {
  background-color: #579e18; }

.db-movie__info {
  width: 60%; }



