.db-add-movie {
  transition: max-height 1s ease-in;
  max-height: 1500px;
  overflow: hidden; }

.db-add-movie--hide {
  max-height: 0;
  transition: max-height .5s ease-out; }

.db-add-movie__form {
  display: flex;
  flex-wrap: wrap; }

.db-add-movie__title {
  color: grey;
  margin-bottom: 30px; }

.db-add-movie__title:after {
  content: '';
  display: block;
  width: 300px;
  height: 2px;
  background-color: grey; }

.db-add-movie__main-params {
  display: flex;
  flex-direction: column;
  color: #a7b6b7;
  margin-right: 40px; }

.db-add-movie__error {
  color: red;
  font-weight: 700; }

.db-add-movie__img-upload {
  margin-left: 100px; }

.db-add-item__text-area {
  width: 300px;
  height: 150px; }

.db-add-movie__drop-files {
  border: 2px dashed white;
  width: 200px;
  height: 100px; }

.db-add-movie__dropped-image {
  width: 100%;
  height: 100%; }

.db-add-movie__dropped {
  border: 2px solid white;
  width: 200px;
  background-color: palevioletred;
  height: 100px; }

.db-add-movie__submit {
  padding: 5px 25px;
  cursor: pointer;
  color: #fff9f9;
  background-color: #289b39;
  border: 1px solid #e3fce6;
  margin-right: 10px; }

.db-add-movie__cancel {
  padding: 5px 10px;
  cursor: pointer;
  color: grey;
  background-color: #fc3232;
  border: 1px solid #f9efef; }

.db-add-movie__buttons {
  margin-top: 40px; }

.db-add-movie__submit--disabled {
  background: darkgrey; }



