.db-tv-show {
  color: grey; }

.db-tv-show__container {
  width: 90%;
  margin-left: 5%; }

.db-tv-show__image {
  width: 200px;
  height: 300px;
  margin-right: 30px; }

.db-tv-show__image img {
  width: inherit;
  height: inherit; }

.db-tv-show__flex {
  display: flex;
  flex-wrap: wrap; }

.db-tv-show__flex--column {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap; }

.db-tv-show__desc {
  overflow: auto;
  height: 250px;
  width: 100%; }

.db-tv-show__name {
  margin: 40px 0;
  font-size: 25px; }

.db-tv-show__recommended {
  display: flex;
  width: 100%;
  overflow-x: scroll;
  height: 400px; }

.db-tv-show__info {
  width: 60%; }



